export default class ClientView extends React.Component {

    constructor(props) {
        super(props)

        this.state = {

            selected: {},
            filter: false,
            rows: [],
            length: null


        }


    }

    filterTypes = Object.assign({}, ReactDataGrid.defaultProps.filterTypes, {
        dogs: {
            type: 'string',
            emptyValue: '',
            operators: [
              {
                name:'contains',
                fn: ({ value, filterValue, data }) => {
                 debugger
                    return !filterValue ?
                    true :
                 value && value.map(i=> {if (i.dog_name.toLowerCase().indexOf(filterValue.toLowerCase()) != -1){return true}})[0];
                }
                 }   ],
        }
          });


    columns = [
        {
            name: 'name', header: 'Name', defaultWidth: 200,
        },
        {
            name: 'email', header: 'E-mail', defaultWidth: 280, render: ({ value }) => {

                return (<a href={"mailto:" + value} >{value}</a>)
            }
        },
        {
            name: 'phone', header: 'Phone', defaultWidth: 150,
        },
        {
            name: 'dog', header: 'Dog', defaultWidth: 200, render: ({ value, data }) => {
                try {
                    return <>
                        { value.map(i => {

                            return (<a href={'#dogs/' + i.id} ><span style={{ paddingRight: '5px' }}>{i.dog_name}</span></a>)

                        })}
                    </>
                }
                catch { return }


            }
        },
        {
            name: 'instagram', header: 'Instagram', defaultWidth: 150,
        },

    ];

    gridStyle = { minHeight: 550 };

    defaultFilterValue = [


        { name: 'phone', operator: 'contains', type: 'string', value: '' },
        { name: 'email', operator: 'contains', type: 'string', value: '' },
        { name: 'name', operator: 'contains', type: 'string', value: '' },
        {name: 'dog', operator: 'contains', type: 'dogs', value: ''}, 
        { name: 'instagram', operator: 'contains', type: 'string', value: '' },

    ];




    async componentDidMount() {
        let clients = await dataProvider.getList('clients', { 'pagination': { "page": 1, "perPage": 1000000 } })


        let clientsWDogs = await Promise.all(clients.data.map(async client => {
            let dog = await dataProvider.getManyReference('dogs', { "filter": { "owner": client.id }, 'pagination': { "page": 1, "perPage": 1000000 } })
            client.dog = dog.data

            return client

        }))

        this.setState({ rows: clientsWDogs, length: clients.total })



        this.subs = firebase.firestore().collection('clients').onSnapshot(a => {

            if (a.docChanges().length !== this.state.length) {
                a.docChanges().forEach(change => {
                    if (change.type === 'added'){
                        this.create(change.doc)
                    }


                   else if (change.type === 'modified') {
                        this.update(change.doc)
                    }


                    
                })
            }



        })

    }
    create =(event) => {
        let rows = this.state.rows
        let id = event.id
        let even = event.data()
        even.id = id
        rows.push(even)
    }

    update = (event) => {
        let rows = this.state.rows
        debugger
        let id = event.id
        let index = this.state.rows.findIndex(i => i.id === id)
        rows[index] = event.data()
        rows[index].id = id
  
        this.setState({ rows: rows })
    }

    componentWillUnmount() {

        this.subs()
    }






    render() {
        return (
            <>
                <Title title='Clients' />
                <div>
                    <Button
                        label="Filters"
                        onClick={() => this.setState({ filter: !this.state.filter })}>
                        <FilterListIcon />
                    </Button>
                    {Object.keys(this.state.selected).length === 1 && <EditButton
                        basePath='/clients' label='Edit' record={Object.values(this.state.selected)[0]}
                    />}
                    {Object.keys(this.state.selected).length !== 0 && <Button
                        label="Message Client">
                        <SendIcon />
                    </Button>}
                </div>
                <ReactDataGrid
                    idProperty="id"
                    columns={this.columns}
                    dataSource={this.state.rows}
                    filterTypes={this.filterTypes}
                    style={this.gridStyle}
                    defaultFilterValue={this.defaultFilterValue}
                    onSelectionChange={select => this.setState({ selected: select.selected })}
                    enableFiltering={this.state.filter}
                    multiSelect
                    checkboxColumn
                />
            </>
        )
    }
}










const PostTitle = ({ record }) => {
    return <span>Client {record ? `"${record.name}"` : ''}</span>;
}

export const UserCreate = props => (
    <Create {...props}>
        <SimpleForm>
            <TextInput source="name" label='Name' />
            <TextInput source="phone" label="Phone" />
            <TextInput source="email" label="Email" />
            <TextInput source="instagram" label="Instagram" />
        </SimpleForm>
    </Create>
);

export const UserEdit = props => (
    <Edit title={<PostTitle />} {...props} >
        <SimpleForm>
            <TextInput source="name" label='Name' />
            <TextInput source="phone" label="Phone" />
            <TextInput source="email" label="Email" />
            <TextInput source="instagram" label="Instagram" />
        </SimpleForm>
    </Edit>
);



