import React from 'react';
import ReactDataGrid from '@inovua/reactdatagrid-community';
import '@inovua/reactdatagrid-community/index.css';
import NumberFilter from '@inovua/reactdatagrid-community/NumberFilter'
import DateFilter from '@inovua/reactdatagrid-community/DateFilter'
import SelectFilter from '@inovua/reactdatagrid-community/SelectFilter'
import CheckBox from '@inovua/reactdatagrid-community/packages/CheckBox'
import { dataProvider, getList } from '../components/authProvider'
import SendIcon from '@material-ui/icons/Send';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import { NotificationManager } from 'react-notifications';
import {
  EditButton,
  Button
} from 'react-admin';
import moment from 'moment';
import Chip from '@material-ui/core/Chip'
import FilterListIcon from '@material-ui/icons/FilterList';
import { Title } from 'react-admin';
window.moment = moment
var dateFormat = require("dateformat");




export default class BookingView extends React.Component {

  constructor(props) {
    super(props)

    this.state = {

      selected: {},
      filter: false,
      serviceFilter: [],


    }
  }


  getService = () => {
    return dataProvider.getList('services', { 'pagination': { "page": 1, "perPage": 100000 } }).then(response => {

      return response.data.map(c => { return { id: c.service, label: `${c.service}-${c.category}` } })
    })
  }

  columns = [
    {
      name: 'name', header: 'Name', defaultWidth: 200, render: ({ value, data }) => {

        if (data['client_ref']) {

          return (<a href={'#clients/' + data['client_ref']} >{value}</a>)

        }
        else
          return (<span >{value}</span>)

      }
    },
    {
      name: 'dogName', header: 'Dog', defaultWidth: 150, render: ({ value, data }) => {
        if (data['dog_ref']) {
          return (<a href={'#dogs/' + data['dog_ref']} >{value}</a>)
        }
        else
          return (<span >{value}</span>)
      }
    }, {
      name: 'status', header: 'Status', defaultWidth: 101, render: ({ value, data }) => {
        if (value === 'confirmed') {
          return (
            <Chip color='primary' style={{ backgroundColor: 'green' }} label='Confirmed' key={value} />
          )
        }
        else if (value === 'modified') {
          return (
            <Chip color='primary' style={{ backgroundColor: 'orange' }} label='Modified' key={value} />
          )
        }
        else if (value === 'cancelled') {
          return (
            <Chip color='primary' style={{ backgroundColor: 'red' }} label='Cancelled' key={value} />
          )
        }
      }
    },
    {
      name: 'service', header: 'Service', defaultWidth: 250,
      render: ({ value, data }) => {
        return (<span >{value || ''} {data.category || ''}</span>)
      },
      filterEditor: SelectFilter,
      filterEditorProps: {
        multiple: true,
        wrapMultiple: false,
        dataSource: this.getService(),
      }
    },
    {
      name: 'date', header: 'Booking Date', defaultWidth: 138, filterEditor: DateFilter, filterEditorProps: (props, { index }) => {

        return ({
          dateFormat: 'ddd DD/MM/YYYY',
          placeholder: index === 1 ? 'Bookings date is before...' : 'Booking date is on...'
        })
      },
      render: ({ value, cellProps: { dateFormat } }) => {
        return moment(value).format('ddd DD/MM/YYYY')
      },
    },
    {
      name: 'end', header: 'Time', defaultWidth: 150, filterEditor: DateFilter, filterEditorProps: (props, { index }) => {

        return ({
          dateFormat: 'ddd DD/MM/YYYY',
          placeholder: index === 1 ? 'Bookings date is before...' : 'Booking date is on...'
        })
      },
      render: ({ value, data }) => {

        return `${moment(data.start).format('hh:mm A')}-${moment(value).format('hh:mm A')}`
      },
    },
    {
      name: 'addOns', header: 'AddOns', defaultWidth: 180, render: ({ value }) => {
        if (value.length !== 0) {

          return (
            <>
              {value.map(item =>

                <Chip color='secondary' label={item} key={item} />

              )}
            </>
          )

        }
        else
          return (<span></span>)
      },
      cellDOMProps: ({ data, value }) => {
        if (value.length > 2) {
          return {
            onMouseEnter: () => {
              NotificationManager.success(value.map(item => { return <>{item}<br /></> }), 'Extras', 5000, () => { alert(value.map(i => `${i}\n`)) });
            }
          }
        }
      }
    },
    { name: 'code', header: 'Booking Code', defaultWidth: 110 },
  ];

  gridStyle = { minHeight: 550 };

  defaultFilterValue = [

    { name: 'date', operator: 'eq', type: 'date', value: '' },
    { name: 'service', operator: 'inlist', type: 'select', value: null },
    { name: 'code', operator: 'contains', type: 'string', value: '' },
    { name: 'dogName', operator: 'contains', type: 'string', value: '' },
    { name: 'name', operator: 'contains', type: 'string', value: '' },
  ];










  render() {
    return (
      <>
        <Title title={this.props.title} />
        <div>
          <Button
            label="Filters"
            onClick={() => this.setState({ filter: !this.state.filter })}>
            <FilterListIcon />
          </Button>
          {Object.keys(this.state.selected).length === 1 && <EditButton
            basePath='/bookings' label='Edit' record={Object.values(this.state.selected)[0]}
          />}
          {Object.keys(this.state.selected).length === 1 && <Button
            label="Charge card">
            <MonetizationOnIcon />
          </Button>}
          {Object.keys(this.state.selected).length !== 0 && <Button
            label="Message Owners">
            <SendIcon />
          </Button>}
        </div>
        <ReactDataGrid
          idProperty="id"
          columns={this.columns}
          dataSource={this.props.rows}
          style={this.gridStyle}
          defaultFilterValue={this.defaultFilterValue}
          onSelectionChange={select => this.setState({ selected: select.selected })}
          enableFiltering={this.state.filter}
          multiSelect
          checkboxColumn
        // pagination='local'
        // dataSource={this.handleCallback}
        />
      </>
    )
  }
}