import React from 'react';
import BookingView from './BookingView'
import {dataProvider} from '../components/authProvider'
import moment from 'moment';
window.moment = moment
var dateFormat = require("dateformat");
export default function GroomingToday() {

  const [rows, setRows] = React.useState([])

  React.useEffect(()=>{
    let today = new Date()

    dataProvider.getList('bookings', { 'pagination': { "page": 1, "perPage": 1000000 }, filter:{'category':'stylist','start': dateFormat(today, "yyyy-mm-dd")} }).then(response => {
      response.data.forEach(i => { i.date = i.start.substring(0, 10); })
    
      setRows(response.data)
    })
  },[])

  return (
    <>
    <BookingView rows={rows} title='Grooming Today'/>
    </>
  );
}



