export default class DogView extends React.Component {

    constructor(props) {
        super(props)

        this.state = {

            selected: {},
            filter: false,
            rows: [],
            length: null


        }



    }


    filterTypes = Object.assign({}, ReactDataGrid.defaultProps.filterTypes, {
        client: {
            type: 'string',
            emptyValue: '',
            operators: [
              {
                name:'contains',
                fn: ({ value, filterValue, data }) => {
                             debugger
                    return !filterValue ?
                    true :
                 value && value.name.toLowerCase().indexOf(filterValue.toLowerCase()) != -1;
                }
                 }  
         ],
        }
          });

      
    columns = [
        {
            name: 'dog_name', header: 'Name', defaultWidth: 200,
        },
        {
            name: 'client', header: 'Client', defaultWidth: 200, filterEditor: StringFilter,render: ({ value, data }) => {
                try {
                    return <>
                     

                         <a href={'#clients/' + value.id} ><span style={{ paddingRight: '5px' }}>{value.name}</span></a>

                        
                    </>
                }
                catch { return }


            },
            
        },
        {
            name: 'breed', header: 'Breed', defaultWidth: 200,  render:({value})=>{ 
             return  value &&  (<Chip color='primary' style={{ backgroundColor: 'orange' }} label={value} key={value} />)}
        },
        {
            name: 'vet_email', header: 'Vet Email', defaultWidth: 200,
        },
        {
            name: 'vet', header: 'Vet', defaultWidth: 200
        },
        {
            name: 'DaycareAttendee', header: 'Daycare Attendee', defaultWidth: 150, filterEditor:BoolFilter ,render:({value}) =>{
                return value && (< DoneIcon htmlColor='green' style={{textAlign:'center',verticalAlign:'center'}}/> )
            }
        },
        {
            name: 'dogdob', header: 'DOB',filterEditor:DateFilter, defaultWidth: 200,
        },
        {
            name: 'comments', header: 'Comments', defaultWidth: 200,
        },
        {
            name: 'sex', header: 'Sex', defaultWidth: 100, filterEditor: SelectFilter,
            filterEditorProps: {
              multiple: true,
              wrapMultiple: false,
              dataSource: ["Male", 'Female'].map(c => {
                return { id: c, label: c}
              }),
            }
        },
        {
            name: 'desexed', header: 'Desexed', defaultWidth: 100, render:({value}) =>{
                return value && (< DoneIcon htmlColor='pink' style={{textAlign:'center',verticalAlign:'center'}}/> )
            },  filterEditor:BoolFilter
        },
        {
            name: 'PassedTemperamentTrial', header: 'Passed Temperament Trial', defaultWidth: 150, filterEditor:BoolFilter ,render:({value}) =>{
                return value && (< DoneIcon htmlColor='blue' style={{textAlign:'center',verticalAlign:'center'}}/> )
            }
        },
        {
            name: 'kcDue', header: 'KC Due', defaultWidth: 200,filterEditor:DateFilter
        },
        {
            name: 'c3Due', header: 'C3 Due', defaultWidth: 200,filterEditor:DateFilter
        },





    ];

    gridStyle = { minHeight: 550 };

  
    defaultFilterValue = [



        { name: 'dog_name', operator: 'contains', type: 'string', value: '' },
        { name: 'client', operator: 'contains', type: 'client', value: '' },
        { name: 'breed', operator: 'contains', type: 'string', value: '' },
        { name: 'vet_email', operator: 'contains', type: 'string', value: '' },
        { name: 'vet', operator: 'contains', type: 'string', value: '' },
        { name: 'DaycareAttendee', operator: 'eq', type: 'boolean', value: null },
        { name: 'dogdob', operator: 'beforeOrOn', type: 'date', value: ''},
        { name: 'comments', operator: 'contains', type: 'string', value: '' },
        { name: 'sex', operator: 'inlist', type: 'select', value: null },
        { name: 'desexed', operator: 'eq', type: 'boolean', value: null },
        { name: 'PassedTemperamentTrial', operator: 'eq', type: 'boolean', value: null },
        { name: 'kcDue', operator: 'beforeOrOn', type: 'date', value: ''},
        { name: 'c3Due', operator: 'beforeOrOn', type: 'date', value: ''},
    ];




    async componentDidMount() {
        debugger
        let dogs = await dataProvider.getList('dogs', { 'pagination': { "page": 1, "perPage": 1000000 } })


         await Promise.all(dogs.data.map(async dog => {
            
            let client = await dataProvider.getManyReference('clients', { "filter": { "id": dog.owner }, 'pagination': { "page": 1, "perPage": 1000000 } })
            dog.client = client.data[0]

            return 

        }))
        

        this.setState({ rows: dogs.data, length: dogs.total })


    }




    componentWillUnmount() {

    //    this.subs()
    }






    render() {
        return (
            <>
                <Title title='Dogs' />
                <div>
                    <Button
                        label="Filters"
                        onClick={() => this.setState({ filter: !this.state.filter })}>
                        <FilterListIcon />
                    </Button>
                    {Object.keys(this.state.selected).length === 1 && <EditButton
                        basePath='/dogs' label='Edit' record={Object.values(this.state.selected)[0]}
                    />}
                    {Object.keys(this.state.selected).length !== 0 && <Button
                        label="Message Client">
                        <SendIcon />
                    </Button>}
                </div>
                <ReactDataGrid
                    idProperty="id"
                    columns={this.columns}
                    filterTypes={this.filterTypes}
                    dataSource={this.state.rows}
                    style={this.gridStyle}
                    defaultFilterValue={this.defaultFilterValue}
                    onSelectionChange={select => this.setState({ selected: select.selected })}
                    enableFiltering={this.state.filter}
                    multiSelect
                    checkboxColumn
                />
            </>
        )
    }
}









